.parts-buy {
  padding-top: 21px;
  padding-bottom: 43px;
  .product-photos__wrapper {
    margin-bottom: 55px;
  }
  .product-attr {
    padding-bottom: 53px;

    &__formItem {
      margin-right: 8px;
      display: flex;
      align-items: center;
      .select-wrapper {
        width: 90px;
      }
      .input-wrapper {
        width: 85px;
      }
      .formItem-des {
        margin-left: 8px;
        line-height: 1.5;
        color: $color-black;
      }
    }

    &__integral {
      margin-right: 28px;
      display: inline-block;
      color: $color-warning;
    }
  }
  .product-detail__wrapper {
    margin-bottom: 43px;
  }
}
