.form-layout {
  &__inline {
    &.row {
      margin: 0;
    }
    .form-item {
      padding: 0 41px 0 0;
      display: inline-block;
      &-label {
        display: inline-block;
      }
      &-control-wrapper {
        display: inline-block;
      }
    }
  }
  .form-item {
    &:not(:last-child) {
      margin-bottom: 34px;
    }
    &-label {
      padding: 0 7px 0 0;
      text-align: right;
      label {
        line-height: 36px;
        margin-bottom: 0;
        color: $color-black;
        font-size: 16px;
        font-weight: 500;
      }
    }
    &-control-wrapper {
      padding: 0;
    }
    &-control {
      line-height: 36px;
      color: $color-violet;
    }

    &__inline {
      display: inline-block;
      .form-item-label {
        display: inline-block;
      }
      .form-item-control-wrapper {
        display: inline-block;
      }
    }
  }
}