/* ==========================================================================
   #BUTTONS
   ========================================================================== */

/**
 * This is an example component. Extend inuitcss by building your own components
 * that make up your UI. Component classes are prefixed with a `c-`.
 */

/**
 * 1. Allow us to style box model properties.
 * 2. Line different sized buttons up a little nicer.
 * 3. Make buttons inherit font styles (often necessary when styling `input`s as
 *    buttons).
 * 4. Reset/normalize some styles.
 * 5. Force all button-styled elements to appear clickable.
 */

.c-btn {
  display: inline-block; /* [1] */
  vertical-align: middle; /* [2] */
  font: inherit; /* [3] */
  text-align: center; /* [4] */
  margin: 0; /* [4] */
  cursor: pointer; /* [5] */
  width: 96px;
  height: 36px;
  line-height: 36px;
  color: #fff;
  border: none;
  border-radius: 18px;
  background: #F44336;
  display: inline-block;
  transition: $global-transition;
}

/* Style variants
   ========================================================================== */

.c-btn__primary {
  background-color: $color-primary;

  &:hover,
  &:active,
  &:focus {
    text-decoration: none; /* [4] */
    color: $color-white;
  }

  &:hover,
  &:focus {
    background-color: darken($color-primary, 10%);
  }
}

.c-btn__success {
  background: #009cff;
  background: linear-gradient(90deg, #0067FF 0%, #00C6FF 100%);

  &:hover,
  &:active,
  &:focus {
    text-decoration: none; /* [4] */
    color: $color-white;
  }

  &:hover,
  &:focus {
    background-color: darken(#009cff, 10%);
    background: linear-gradient(90deg, #005aff 0%, #00c0ff 100%);
  }
}

.c-btn__disabled {
  background: $color-very-light-grey;

  &:hover,
  &:active,
  &:focus {
    text-decoration: none; /* [4] */
    color: $color-white;
  }

  &:hover,
  &:focus {
    background-color: darken($color-very-light-grey, 10%);
  }
}

/* Size variants
   ========================================================================== */

.c-btn--small {
  height: 30px;
  line-height: 30px;
}

.c-btn--large {
  width: 154px;
  height: 58px;
  line-height: 58px;
  border-radius: 29px;
}

.c-btn__block {
  width: 100%;
  border-radius: 4px;
}

.btn-combine {
  height: 26px;
  width: 106px;
  border-radius: 13px;
  background: #fff;
  display: inline-block;
  overflow: hidden;
  line-height: 1;
  a {
    display: inline-block;
    width: 50%;
    line-height: 26px;
    font-size: 14px;
    color: $color-primary;
    text-align: center;
    text-indent: 2px;
  }
  .color-dark {
    color: #fff;
    
    position: relative;
    &::before {
      content: '';
      background: #3E3A3A;
      width: 100%;
      height: 100%;
      position: absolute;
      transform: skewX(-10deg);
      z-index: 0;
      left: 1px;
      transition: $global-transition;
    }
    &:hover:before{
      background: #000;
    }

    span {
      position: relative;
      z-index: 2;
    }
  }
}
