.pop-up {
  max-width: 900px;
  &-form {
    background: #F9F9F9;
    padding: 24px 0;
    &__inner {
      width: 442px;
      margin: auto;
    }
    .form-control {
      width: 288px;
    }
  }
  &-title {
    background: $color-primary;
    color: #fff;
    padding: 14px 0;
    font-size: 22px;
    span {
      display: inline-block;
    }
    .navbar-brand__white {
      margin-top: 0;
      vertical-align: middle;
      position: relative;
      top: -3px;
    }
    .close {
      margin-right: 18px;
      outline: none;
      transition: $global-transition;
    }
  }

  &-content {
    padding: 30px 40px;

    .form-group {
      label {
        text-align: right;
        vertical-align: top;
        padding-right: 0;
        line-height: 1;
        .row-icon {
          margin-right: 7px;
          position: relative;
          top: -2px;
        }
      }

      .w130 {
        width: 130px;
        display: inline-block;
      }
      .w80 {
        width: 80px;
        display: inline-block;
      }

      .form-mini {
        display: inline-block;
        .form-item {
          display: inline-block;
          &:not(:last-child) {
            margin-right: 10px;
          }
          .item-label {
            padding-left: 10px;
          }
          .item-des {
            margin-left: 7px;
          }
        }
      }
    }
  }

  
}

.modal-dialog{
  border-radius: 7px;
  overflow: hidden;
}
