.appo {
  &-item {
    padding-bottom: 39px;
    display: flex;
    &:not(:last-child) {
      margin-bottom: 25px;
      border-bottom: 1px solid #E8EDF2;
    }
  }
  &-index {
    width: 36px;
    height: 36px;
    line-height: 36px;
    margin-right: 24px;
    flex-shrink: 0;
    color: $color-medium-grey;
    font-size: 18px;
    text-align: center;
    background: #E8EDF2;
    border-radius: 100%;
  }
  .w250 {
    width: 550px;
  }
  &-content {
    flex: 1;
    &__fixed {
      flex: 0 1 550px;
    }
    &-row {
      font-size: 14px;
      &:not(:last-child) {
        margin-bottom: 46px;
      }
    }
    &__left {
      width: 75px;
      display: inline-block;
      text-align: right;
      color: $color-dark-grey;
      font-weight: 500;
    }
    &__right {
      color: $color-medium-grey;
      word-wrap: break-word;
    }
  }
}
