/* ==========================================================================
   #HEADER
   ========================================================================== */

.c-header {
  height: 80px;
  background: #fff;

  .navbar-brand {
    font-weight: bold;
    padding: 0;
    text-indent: -9999px;
    display: inline-block;
    height: 30px;
    width: 163px;
    margin-top: 28px;
    background-image: url('../assets/images/logo.png');
    background-repeat: no-repeat;
    background-position: 0 -31px;
    &__white {
      color: #fff;
      background-position: 0 0;
    }
  }
  
  .navbar-nav {
    line-height: 80px;
    display: inline-block;
    li {
      display: inline-block;
      margin-bottom: 0;
      padding: 0 20px;
      .nav-item {
        line-height: 1;
        font-size: 16px;
        color: #3f3a3a;
        padding-bottom: 10px;
        display: inline-block;
        position: relative;
        &.active {
          border-bottom: 2px solid red;
        }
        &:hover {
          &::before{
            width: 100%;
          }
        }
      }
      .nav-item::before {
        content: '';
        position: absolute;
        width: 0;
        left: 0;
        bottom: -2px;
        height: 2px;
        background: red;
        transition: width .2s ease-in-out;
      }
    }
  }

  .btn-combine {
    position: relative;
    top: 8px;
  }

  .select-city__wrapper {
    width: 55px;
    line-height: 1;
    display: inline-block;
    .form-control {
      background: none;
    }
    .select-city {
      color: #3f3a3a;
      border: none;
      background: none;
    }
  }

  &__fixed {
    position: absolute;
    width: 100%;
    z-index: 99;
    background: transparent;
    .navbar-brand {
      color: #fff;
      background-position: 0 0;
    }
    .navbar-nav {
      li {
        .nav-item {
          color: #fff;
          &.active {
            border-bottom: 2px solid #fff;
          }
          &:hover {
            &::before{
              width: 100%;
            }
          }
        }
        .nav-item::before {
          background: #fff;
        }
      }
    }
    .select-city__wrapper {
      .select-city {
        color: $color-white;
      }
    }
  }
}

.c-header {
  .btn-combine {
    background: $color-primary;
    a{
      color: #fff;
    }
  }
}

.c-header__fixed {
  .btn-combine {
    background: #fff;
    a:first-child{
      color: $color-primary;
    }
  }
}
