.user-service , .user-order {
  .tabs-list {
    margin-bottom: 23px;
    padding: 0 16px;
    display: flex;
    border-bottom: 1px solid #E8EDF2;
    .tab-item {
      margin-bottom: 0;
      line-height: 1;
      padding: 0 10px 17px;
      color: $color-medium-grey;
      font-size: 14px;
      text-align: center;
      box-sizing: border-box;
      cursor: pointer;
      &:not(:last-child) {
        margin-right: 50px;
      }
      &.active {
        color: $color-dark-grey;
        font-weight: 500;
        border-bottom: 2px solid $color-primary;
      }
    }
  }
}

.user-record-table {
  font-size: 14px;
  th {
    font-weight: 500;
    border: none !important;
    &.w-190 {
      width: 190px;
    }
  }
  td {
    padding: 19px;
    border: none;
    color: $color-light-grey;
    &.td-bling {
      color: $color-warning;
    }
  }
}
