.car-slider {
  margin: 0 auto;
  position: relative;
  .view {
    .swiper {
      &-container {
        width: 980px;
        height: 535px;
        background: #fff;
        img {
          height: auto;
          width: auto;
        }
      }
      .swiper-slide {
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
  .preview {
    width: 980px;
    margin: 0 auto;
    text-align: center;
    .preview-wrapper {
      position: relative;
      display: inline-block;
      .swiper {
        &-container {
          width: auto;
          max-width: 800px;
          height: 120px;
        }
        &-wrapper {
          display: flex;
        }
        &-slide {
          width: 162px;
          height: 120px;
          opacity: 0.4;
          .car-pic {
            width: 140px;
            height: 120px;
            border: 1px solid $color-very-light-grey;
            box-sizing: border-box;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
          }
          &.active-nav{
            opacity: 1;
            .car-pic {
              border: 1px solid $color-primary;
            }
          }
        }
      }
      .arrow-left {
        position: absolute;
        left: -32px;
        top: 50%;
        transform: translateY(-50%);
        z-index: 1;
      }
      .arrow-right {
        position: absolute;
        right: -12px;
        top: 50%;
        transform: translateY(-50%);
        z-index: 1;
      }
    }
  }
}
