.product-card {
  background: #fff;
  margin-bottom: 20px;
  transition: all .2s linear;
  border-radius: 7px;
  &.collect {
    -webkit-box-shadow: 0 1px 5px #f6f6f6;
    box-shadow: 0 1px 5px #f6f6f6;
  }
  &:hover {
    -webkit-box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1) !important;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1) !important;
    -webkit-transform: translate(0, -2px);
    transform: translate(0, -2px);
  }

  &-img {
    text-align: center;
    height: 250px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    a {
      height: 100%;
      display: block;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    img {
      max-height: 100%;
    }
  }

  &-content {
    padding: 14px 32px;
    font-size: 14px;
  }

  &-title {
    font-size: 16px;
    margin-bottom: 5px;
    font-weight: bold;

    .float-right {
      font-size: 14px;
      strong{
        font-weight: normal;
      }
      font-weight: normal;
    }
  }

  &-intro {
    color: $color-medium-grey;
    margin-top: 10px;
    line-height: 20px;
    @include no-wrap-d(2);
    strong {
      color: #333;
    }
  }

  &-btm {
    margin-top: 10px;
  }

  &-price {
    font-size: 16px;
    strong {
      font-size: 22px;
      padding-right: 5px;
      color: $color-primary;
    }
    .icon {
      width: 20px;
      height: 20px;
      position: relative;
      top: -2px;
    }
  }

  &-collect {
    cursor: pointer;
    .icon-collect {
      width: 22px;
      height: 18px;
      display: inline-block;
      position: relative;
      top: 3px;
      background-image: url('../assets/images/common/icon-fav.png');
      background-repeat: no-repeat;
      background-size: 44px 18px;
      background-position: 0 0;
      &.collected {
        background-position: -22px 0;
      }
    }
  }

  &-tip {
    color: $color-primary;
    font-size: 16px;
  }

  &__small {
    .product-card-img {
      height: 120px;
      width: auto;
    }
    .product-card-content {
      .col {
        padding-left: 0;
        padding-right: 0; 
      }
    }
  }
}

.top-auction {
  .product-card-img {
    border-bottom: 1px solid #f0f0f0;
    position: relative;
    &::after {
      content: '';
      width: 0;
      position: absolute;
      bottom: 0;
      left: 0;
      height: 1px;
      background: $color-primary;
      transition: $global-transition;
    }
  }
  .product-card:hover {
    .product-card-img::after {
      width: 100%;
    }
  }
  .product-card-content {
    padding: 16px 16px;
  }

  .top-auction-row {
    display: flex;
  }
  .col-small {
    width: 650px;

    .product-card-content {
      padding: 8px 16px;
    }
    .top-auction-col {
      flex: 1;
      margin: 0 15px;

      &:first-child {
        margin-right: 0;
      }
    }
  }
  .col-big {
    flex:1;
  }

}
