.sign {
  &-header {
    display: inline-block;
    font-size: 22px;
    color: $color-white;
    text-align: center;
    background: $color-primary;
    position: relative;
    .close-wrapper {
      line-height: 0;
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translate(0, -50%);
      cursor: pointer;
      img {
        width: 18px;
      }
    }
  }
  &-body {
    padding: 30px 0;
    .capsule {
      margin: 0 auto;
      width: 200px;
      display: flex;
      border-radius: 40px !important;
      overflow: hidden;
      text-align: center;
      & > li {
        margin-bottom: 0;
        height: 40px;
        line-height: 40px;
        flex: 1;
        color: $color-light-grey;
        background: #eeeeee;
        cursor: pointer;
        &.active {
          color: $color-primary;
        }
        &:not(:last-child) {
          border-right: 1px solid #e3e3e3;
        }
      }
    }
  }

  .login-form,
  .register-form {
    padding: 45px 90px;

    .form-group {
      margin-bottom: 20px;
    }
  }

  &-forget {
    color: $color-read;
    cursor: pointer;
  }

  .actions {
    .login-btn,
    .register-btn {
      width: 100%;
      height: 43px;
      line-height: 43px;
      border-radius: 4px;
    }
  }

  .code-wrapper {
    .send-btn {
      color: $color-primary;
      background: #eeeeee;
      cursor: pointer;
    }
  }
}
