.brand-models-buy {
  padding-top: 21px;
  padding-bottom: 45px;
  .product-photos__wrapper {
    margin-bottom: 55px;
  }
  .product-attr {
    margin-bottom: 53px;

    &__additional {
      padding: 0 28px;
      margin: 30px 0 57px;
    }
  
    &__exchange-num {
      width: 120px;
    }

    &__formItem {
      margin-right: 8px;
      display: flex;
      align-items: center;
      .select-wrapper {
        width: 130px;
      }
      .input-wrapper {
        width: 85px;
      }
      .formItem-des {
        margin-left: 8px;
        line-height: 1.5;
        color: $color-black;
      }
    }

    
    &__region {
      display: inline-block;
      margin-right: 41px;
    }
  
    &__integral {
      margin-right: 28px;
      display: inline-block;
      color: $color-violet;
    }
  }
}
