.footer-feature {
  padding: 42px 0;
  .col {
    display: flex;
  }
  &-icon {
    display: flex;
    float: left;
    width: 76px;
    height: 76px;
    border-radius: 50%;
    overflow: hidden;
    background: #fff;
    vertical-align: top;
    margin-right: 10px;
    justify-content: center;
    align-items: center;
    span {
      background-image:url('../assets/images/common/icon-features.jpg');
      display: inline-block;
      width: 46px;
      height:  46px;
    }

    .icon {
      &-brand {
        background-position: -48px 0; 
      }
      &-repair {
        background-position: -96px 0; 
      }
      &-service {
        background-position: -145px 0; 
      }
    }

  }
  &-content {
    display: inline-block;
    max-width: 230px;
    flex:1;
    h3 {
      font-size: 16px;
    }
    p {
      font-size: 14px;
      color: #9B9B9B;
      margin-top: 5px;
    }
  }
}