.product-detail {
  display: flex;
  align-items: stretch;
  &__left {
    width: 603px;
    height: 273px;
    flex-shrink: 0;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;   
  }
  &__right {
    padding: 0 29px;
    line-height: 25px;
    display: flex;
    flex-direction: column;
    color: $color-light-grey;
    font-size: 14px;
    .product-txt {
      margin-bottom: 29px;
      flex: 1;
      overflow-y: auto;
    }
    .actions {
      flex-shrink: 0;
      font-size: 16px;
      text-align: right;
      .buy-btn {
        line-height: 29px;
        width: 154px;
        height: auto;
        border-radius: 29px;
        .tip {
          font-size: 14px;
          font-weight: 300;
          opacity: 0.54;
        }
      }
    }
  }
}
