.search-box{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 100px;
  width: 100%;
  z-index: 99;
  &__inner {
    background: #fff;
    width: 690px;
    display: flex;
  }
  input {
    flex:1;
    border: none;
    padding: 0 10px;
    outline: none;
  }
  button {
    background: #FF5A37;
    height: 48px;
    outline: none;
    width: 150px;
    cursor: pointer;
    color: #fff;
    font-size: 16px;
    border:1px solid #fff;
    transition: background .2s ease;
    &:hover {
      background: #fb4b26;
    }
  }
}