.three-box {
  margin-top: -60px;
  .col {
    padding: 0 10px;
  }
  &-item {
    height: 200px;
    line-height: 200px;
    border-radius: 8px;
    background: #ee6657;
    color: #fff;
    overflow: hidden;
    width: 100%;
    transition: all .2s linear;
    &:hover {
      box-shadow: 0 10px 30px rgba(0,0,0,0.1);
    }
    a {
      display: block;
      height: 100%;

      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;  
    }
    img {
      height: 100%;
    }
  }

  .row {
    .col {
      &:nth-child(2) .three-box-item{
        background: #feba54;
      }
      &:nth-child(3) .three-box-item{
        background: #24ce8c;
      }
    }
  }
}