.integral-des {
  .des {
    &-chapter {
      padding: 0 25px 19px ;
      margin-bottom: 23px;
      font-size: 14px;
      &:not(:last-child) {
        border-bottom: 1px solid #E8EDF2;
      }
    }
    &-title {
      margin-bottom: 9px;
      color: $color-dark-grey;
      font-weight: 500;
    }
    &-content {
      margin-bottom: 2px;
      color: $color-light-grey;
    }
  }
}
