.integral-top {
  background: #fff;
  border-radius: 7px;
  margin: 0 -5px;
  overflow: hidden;
  margin-bottom: 20px;
  &-content {
    padding: 30px;
    font-size: 14px;
    background: #FFFDF9;
    h3 {
      font-size: 16px;
      span {
        font-weight: normal;
        font-size: 14px;
        margin-left: 20px;
      }
      &.text-remain{
        label {
          color: #DCB35E;
          font-size: 14px;
        }
      }
    }
    .content-des {
      line-height: 25px;
    }
    .btn {
      border-radius:29px;
      background: $color-primary;
      border: none;
      padding: 5px 30px;
      &:hover {
        background: darken($color-primary, 10%)
      }
      span{
        font-size: 14px;
        color: rgba(255,255,255, .7)
      }
    }
  }
}