.product {
  &-dec__header {
    margin-bottom: 43px;
    .form-layout {
      .form-item {
        margin-bottom: 17px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  &--bling {
    color: $color-warning;
    margin-bottom: 8px;
  }
  &--gray {
    color: $color-light-grey;
  }

  &-guarantee {
    .guarantee-col {
      display: inline-block;
      color: $color-black;
      font-size: 16px;
      &:not(:last-child) {
        margin-right: 50px;
      }
      .icon {
        margin-right: 9px;
        width: 18px;
        height: 18px;
        position: relative;
        top: -3px;
      }
    }
  }

  &-actions {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    .actions__left {
      display: flex;
      align-items: center;
      .collection {
        margin-right: 56px;
        display: inline-block;
        color: $color-light-grey;
        cursor: pointer;
        .icon {
          margin-right: 8px;
          width: 22px;
          height: 20px;
          display: inline-block;
          position: relative;
          top: 1px;
          background-image: url('../assets/images/common/icon-fav.png');
          background-repeat: no-repeat;
          background-size: 46px 20px;
          background-position: 0 0;
        }
      }

    }
    .actions__right {
      display: flex;
      align-items: center;
      .product-worth {
        margin-right: 33px;
        color: $color-black;
        font-size: 16px;
        text-align: center;
        .worth__main {
          margin-bottom: 13px;
          font-weight: 700;
          .icon {
            margin-right: 7px;
            width: 20px;
            height: auto;
            position: relative;
            top: -3px;
          }
          .worth {
            margin-left: 7px;
            color: $color-primary;
            font-size: 22px;
          }
        }
        .worth__tip {
          color: $color-light-grey;
          font-size: 14px;
          .highlight {
            color: $color-primary;
            font-size: 16px;
          }
        }
      }
      .appo {
        font-size: 16px;
        &-icon {
          margin-right: 6px;
          width: 26px;
          height: 24px;
          position: relative;
          top: -3px;
        }
      }
    }
  }

  &-additional {
    display: flex;
    align-items: center;
    .additional-col {
      &:not(:last-child) {
        margin-right: 50px;
      }
      &__left {
        color: $color-dark-grey;
      }
      &__center {
        margin: 0 7px;
        color: $color-primary;
      }
      &__right {
        color: $color-light-grey;
      }
    }
  }
}
