.pagination-box {
  padding: 30px 0;
  margin-bottom: 0;
  ul {
    margin-bottom: 0;
  }
}


.page-item {
  .page-link{
    margin: 0 3px;
    min-width: 32px;
    height: 32px;
    font-size: 14px;
    border-radius: 4px;
    padding: 0;
    text-align: center;
    line-height: 31px;
    color: #333;
    outline: none;
    &.active {
      background: $color-primary;
      color: #fff;
    }
    &.icon {
      &-link  {
        background-position: center;
        background-repeat: no-repeat; 
      }
      &-prev {
        background-image: url(../assets/images/common/icon-page-arrow-l.png); 
      }
      &-next {
        background-image: url(../assets/images/common/icon-page-arrow-r.png); 
      }
    }
  }
}

.page-extra {
  font-size: 14px;
  select {
    outline: none;
    background: #fff;
    height: 32px;
    border: 1px solid #dee2e6;
  }
  input {
    width: 48px;
    text-align: center;
    height: 32px;
  }
}