.product-attr-color {
  margin-bottom: 0;
  display: flex;
  .attr-color__item {
    margin-bottom: 7px;
    cursor: pointer;
    &:not(:last-child) {
      margin-right: 25px;
    }
    .pic-wrapper {
      margin-bottom: 7px;
      width: 50px;
      height: 50px;
      position: relative;
      text-align: center;
      border: 1px solid #000;
      .color-pic {
        width: 50px;
        height: 50px;
      }
      .selected-icon {
        width: 19px;
        height: 19px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: none;
      }
    }
    
    .color-name {
      color: $color-violet;
      text-align: center;
    }

    &.active {
      .selected-icon {
        display: block;
      }
    }
  }
}