.side-btns {
  position: fixed;
  right: 10px;
  top: 20%;
  &-item {
    width: 58px;
    height: 58px;
    border-radius: 6px;
    background: rgba(0,0,0,.5);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff; 
    text-align: center;
    font-size: 12px;
    margin-bottom: 10px;
    cursor: pointer;
    transition: $global-transition;
    &:hover {
      background: rgba(0,0,0,.8);
    }
    img {
      width: 24px;
    }
    p {
      margin-bottom: 0;
      margin-top: 3px;
    }
  }
}
