.type-filter {
  background: #fff;
  padding: 30px 20px;
  margin-bottom: 20px;
  font-size: 14px;
  border-radius: 5px;
  &-row {
    margin-bottom: 15px;
    &:last-child{
      margin-bottom: 0;
    }
  }
  label {
    display: inline-block;
    width: 80px;
    text-align: right;
    margin: 0;
    margin-right: 10px;
  }
  .filter {
    display: inline-block;
    margin-bottom: 0;
    li {
      margin-right: 10px;
      display: inline-block;
      &.active {
        a {
          color: $color-primary;
        }
        .sort-txt {
          color: $color-primary;
        }
      }

      a {
        color: #9B9B9B;
        transition: $global-transition;
        &:hover {
          color: $color-primary;
        }
      }
    }
  }
}
