.receiving-info {
  .title {
    margin-bottom: 29px;
    color: $color-warning;
    font-size: 22px;
    font-weight: 700;
    text-align: center;
  }
  .receiving-form {
    width: 800px;
    margin: 0 auto 19px;
    .input-wrapper {
      width: 240px;
    }
    .form-layout {
      .form-item {
        &:nth-child(odd) {
          margin-right: 192px;
        }
        padding-right: 0;
      }
    }
  }
  .actions {
    text-align: center;
    .exchange-btn {
      width: 442px;
      height: 46px;
      line-height: 46px;
      border-radius: 4px;
    }
  }
}
