.user-info-edit {
  padding: 20px 24px;
  background: $color-white;
  border-radius: 8px;
  display: none;
  &__header {
    padding-bottom: 14px;
    color: $color-dark-grey;
    border-bottom: 1px solid #E8EDF2;
    .cancel-editing {
      display: inline-block;
      cursor: pointer;
      .icon {
        margin-right: 14px;
        width: 24px;
        height: 24px;
        position: relative;
        top: -3px;
      }
    }
  }
  &__body {
    padding-top: 22px;
    font-size: 14px;
    .select-box {
      display: flex;
      align-items: center;
      .select-major {
        line-height: inherit;
        box-sizing: border-box;
      }
      .select-des {
        margin-left: 7px;
        color: $color-light-grey;
      }
    }
  }
  &__footer {
    padding-bottom: 456px;
    font-size: 12px;
    text-align: center;
    .c-btn {
      &:not(:last-child) {
        margin-right: 25px;
      }
    }
  }
}
