.tab-nav {
  background: #fff;
  margin: 0 -5px;
  margin-top: 20px;
  padding: 10px 0;
  border-radius: 7px;
  li {
    display: inline-block;
    margin: 0 20px 0;
    padding: 10px 0;
    font-size: 16px;
    display: inline-block;
    &.active {
      color: $color-primary;
      border-bottom: 2px solid $color-primary;
    }
  }
}
