.order {
  &-item {
    padding-bottom: 28px;
    padding-left: 26px;
    &:not(:last-child) {
      margin-bottom: 28px;
      border-bottom: 1px solid #E8EDF2;
    }
  }
  &-header {
    margin-bottom: 23px;
  }
  &-info {
    display: inline-block;
    &:not(:last-child) {
      margin-right: 33px;
    }
    &__left {
      display: inline-block;
      color: $color-dark-grey;
      font-weight: 500;
    }
    &__right {
      display: inline-block;
      color: $color-medium-grey;
    }
  }

  &-body {
    display: flex;
    align-items: center;
    &__left {
      margin-right: 124px;
      flex-shrink: 0;
      .car-pic {
        margin-right: 18px;
        width: 120px;
        height: 145px;
        display: inline-block;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
      }
      .car-info {
        display: inline-block;
      }
      .car-attr {
        margin-top: 17px;
        .attr-item {
          margin-bottom: 14px;
          text-align: center;
          color: $color-light-grey;
          border: 1px solid $color-light-grey;
          border-radius: 2px;
          &.attr-item__sm {
            width: 50px;
            line-height: 25px;
            display: inline-block;
            &:not(:last-child) {
              margin-right: 20px;
            }
          }
          &.attr-item__lg {
            width: 125px;
            line-height: 25px;
          }
        }
      }
      .car-pick {
        color: $color-primary;
      }
    }

    &__center {
      flex: 1;
      .other-info {
        &-row {
          display: flex;
          &:not(:last-child) {
            margin-bottom: 17px;
          }
          .row__left {
            margin-right: 7px;
            flex-shrink: 0;
            color: $color-dark-grey;
            text-align: right;
            font-weight: 500;
          }
          .row__right {
            color: $color-medium-grey;
          }
        }
      }
    }

    &__right {
      flex-shrink: 0;
    }
  }
}
