.user-center {
  padding: 28px 0 51px;
  .user-info {
    padding: 23px 0 17px;
    position: relative;
    border-radius: 7px;
    background: #fff;
    background-repeat: no-repeat;
    background-position: center top;
    background-size: 100% auto;
    background-image: url('../assets/images/user-bg.jpg');
    &__header {
      margin-bottom: 43px;
      color: $color-white;
      text-align: center;
      .avatar {
        padding-bottom: 41%;
        margin: 0 auto 13px;
        width: 41%;
        height: 1px;
        position: relative;
        img {
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
        }
      }
      .phone {
        margin-bottom: 9px;
        font-size: 16px;
      }
      .drawer {
        margin: 0 17px;
        font-size: 14px;
        border-top: 1px solid $color-bg;
        &-item {
          padding: 8px 0 0;
          display: inline-block;
          box-sizing: border-box;
          &:not(:last-child) {
            position: relative;
            &::after {
              content: '';
              position: absolute;
              width: 1px;
              height: 32px;
              top: 50%;
              right: 0;
              transform: translateY(-50%);
              background: $color-bg;
            }
          }
        }
      }
    }
    &__body {
      margin: 0 17px;
      font-size: 14px;
      .base-col {
        margin-bottom: 13px;
        font-size: 14px;
        &__left {
          padding: 0 7px 0 17px;
          color: $color-dark-grey;
          text-align: right;
        }
        &__right {
          padding: 0 17px 0 7px;
          color: $color-medium-grey;
          word-wrap: break-word;
        }
      }
    }
    &__footer {
      margin: 0 17px;
      font-size: 12px;
      .btn-col {
        padding: 0;
        text-align: center;
      }
      .icon {
        position: relative;
        top: -3px;
      }
    }
    &__setting {
      width: 24px;
      height: 22px;
      position: absolute;
      top: 20px;
      right: 20px;
      cursor: pointer;
    }
  }
}