.join {
  &-header {
    display: inline-block;
    font-size: 22px;
    color: $color-white;
    text-align: center;
    background: $color-primary;
    .navbar-brand {
      margin-top: 0;
      vertical-align: middle;
      position: relative;
      top: -3px;
    }
  }

  &-body {
    padding: 33px 0 24px 0;
  }

  &-actions {
    padding: 0 29px;
  }

  .form-layout {
    padding-right: 30px;
  }
  .form-item {
    margin-bottom: 28px;
    &-label {
      label {
        font-weight: 400;
      }
    }

    .input-wrapper,
    .select-wrapper {
      width: 240px;
    }
  }
}
