.product-attr-brick {
  margin-bottom: 0;
  line-height: 1.5;
  display: flex;
  flex-wrap: wrap;
  .attr-brick__item {
    padding: 3px 12px;
    margin-bottom: 7px;
    flex-shrink: 0;
    color: $color-border;
    border: 1px solid $color-border;
    border-radius: 2px;
    box-sizing: border-box;
    cursor: pointer;
    &:not(:last-child) {
      margin-right: 26px;
    }
    &.active {
      color: $color-primary;
      border: 1px solid $color-primary;
    }
  }
}