.section {
  &-block {
    padding-top: 30px;
  }
  &-title {
    
    position: relative;
    text-indent: 10px;
    vertical-align: baseline;
    margin-bottom: 26px;
    h1 {
      font-size: 22px;
      display: inline-block;
    }
    &::before {
      content: '';
      width: 4px;
      height: 22px;
      background: #F44336;
      transform: skewY(130deg);
      position: absolute;
      left: 0;
      top: 3px;
    }

    a {
      font-size: 14px;
    }
  }
  &-subtitle {
    font-size: 14px;
    display: inline-block;
    margin-left: 5px;
    color: #9B9B9B;
  }
}