/* ==========================================================================
   #FOOTER
   ========================================================================== */

.c-footer {
  background: #404359;
  
  &-links {
    padding: 20px 0;
  }
  h5 {
    color: #fff;
    font-weight: bold;
    font-size: 14px;
  }
  ul {
    li {
      margin-bottom: 0;
      a {
        color: #DFDFDF;
        font-size: 14px;
        &:hover {
          color: #fff;
        }
      }
    }
  }
  .copyright {
    background: #444960;
    color: #dfdfdf;
    font-size: 14px;
    padding: 10px 0;
  }
}
