.sort-wrapper {
  cursor: pointer;
  display: flex;
  align-items: center;
  .sort-txt {
    color: #9B9B9B;
  }
  .lamp {
    margin-left: 7px;
    padding-top: 2px;
    display: inline-block;
    .icon-arr {
      width: 5px;
      height: 5px;
      border-style: solid;
      border-width: 1px 1px 0 0;
      border-color: #dbdbdb;
      &.active {
        border-color: #9B9B9B;
      }
      &__up {
        transform: rotateZ(-45deg);
      }
      &__down {
        transform: rotateZ(135deg);
      }
    }
  }
}
